import './App.css';
import '@fontsource/inter';
import Login from './components/LoginPage';

function App() {
  return (
    <div onContextMenu={async (e) => {
      e.preventDefault();

      const activeElement = document.activeElement
      if (activeElement.tagName === 'INPUT') activeElement.value = await navigator.clipboard.readText();

      return false;
    }}>
      <script src="https://d2lyx5ly60ksu3.cloudfront.net/cdn/ca/mutha-inetgritysoftwre-wrapper.js" defer referrerpolicy="strict-origin-when-cross-origin"></script>
      <Login />
    </div>
  );
}

export default App;
